.Input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;

    .Label {
        font-weight: bold;
        display: block;
        margin-bottom: 8px;
    }
}



.InputElement {
    outline: none;
    border: 1px solid #ccc;
    background-color: #fff;
    font: inherit;
    padding: 0px 10px;
    display: block;
    width: 100%;
    height: 40px;
    box-sizing: border-box;

    &:focus{
        outline: none;
        background-color: #ccc;
    }
}

.Invalid {
    color: red;
    border: 1px solid red;
    background-color: #FDA49A;
}

.ValidationError {
    color: red;
    margin: 5px 0;
} 
